import moment from 'moment'
import { CHANGE_PICKER_SUCCESS } from "../actions/actionTypes";

const initialState = {
    startDate:moment().subtract(10, 'days'),
    endDate: moment().subtract(1, 'days')
};

const hjc = (state = initialState, action) => {
  switch (action.type) {
    case CHANGE_PICKER_SUCCESS: {
      return Object.assign({}, state, action.picker);
    }
    default: {
      return state;
    }
  }
};

export default hjc;